
.container{
    max-width: 1320px;
    margin: 0 auto;
    padding:0 12px;
   
}
@media (max-width:991px){
    .container{
        max-width: 720px;
    }
}
@media (max-width:768px){
    .container{
        max-width: 100%;
    }
}

.header{
    background: #AA0601;
    padding-top: 16px;
    padding-bottom: 16px;
    .nav-bar{
    display: flex;
    justify-content: space-between;
    align-items: center; 
      .logo a{
        background-image:url(../images/logo-white.png);
        width: 80px;
        height: 50px;
        background-size: cover;
        text-indent: 101%;
        overflow: hidden;
        white-space: nowrap;
        display: block;
      }
      .menu{
        display: flex;
        li a{
          color:white;
          display: block;
          font-size: 20px;
          padding: 10px 32px ;
          text-align: center;
          &:hover{
            color:#AA0601;
            background: white;
          }
        }
      }
    }
}

@media (max-width:768px) {
  .header{
    padding-bottom: 0px;
}
    .nav-bar{
        flex-direction: column;
        align-items: center;
        padding-inline: 0;
    }
}

@media (max-width:768px){
    .logo{
        margin-bottom: 12px;
    }
    .menu{
        flex-wrap: wrap;
        width: 100%;
    }
    .menu li{
        width: 50%;
        border: 1px solid white;
       } 
}

.footer{
  background: #AA0601;
  padding-top: 48px;
  padding-bottom: 32px;
  color:#FFFFFF
}
.footer-menu{
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #FFFFFF;
  gap:48px;
}
.menu-list a{
  color:#FFFFFF;
  font-size: 16px;
  display: block;
  padding: 5px 0px;
}
.ml--24{
  margin-left: -24px;
}
.menu-list{
  display: flex;
  justify-content: space-between;
}
.menu-list a:hover{
  background: #FFFFFF;
  color: #AA0601;
}
.social{
  display: flex;
  gap:32px;
}

.social img{
  width: 48px;
  height: 48px;
}
.social img:hover{
  background:white;
  opacity: 0.8;
}
.footer-contact{
  padding-top: 24px;
  padding-bottom: 32px;
}
.footer-contact a{
  color:#FFFFFF;
}
.footer-contact a:hover{
  color:#AA0601;
  background: #FFFFFF;
}

.tel{
  font-size: 36px;
  margin-left: 20px;
}
.mail{
  font-size: 36px;
  margin-left: 20px;
}
.copyright{
  display: flex;
  justify-content: space-between;
  padding-top: 32px;
}
.fs-36{
  font-size: 36px;
}
.other-serve{
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  gap:48px;
}
.other-serve a{
  color:#FFFFFF;
}
.other-serve a:hover{
  color:#AA0601;
  background: #FFFFFF;
}


@media (max-width:991px){
  .social img{
      width: 40px;
      height: 40px;
  }
  
}
@media (max-width:768px){
  .footer{
    padding-top: 24px;
    padding-bottom: 24px;
  }
  .footer-contact{
    padding-bottom: 24px;
  }
  .menu-contact{
     margin-top: -24px;
  }
  .menu-list{
      display: none;
  }
  .tel,.mail{
      font-size:16px ;
      margin-left: 11px;
  }
  .social{
    gap:8px;
  }
  .fs-36{
      font-size: 18px;
  }
  .social img{
      width: 24px;
      height: 24px;
  }
  .copyright{   
      flex-direction: column;
      padding-top: 16px;
  }
  .copyright p{
      margin-bottom: 8px;
  }
  .other-serve{
      flex-direction: column;
      gap:0px
  }
  .other-serve a{
      font-size: 16px;
      padding: 0px 0px;
  }
  .mb-8{
    margin-bottom: 8px;
  }
}